/**
 * @function
 * @name getUrlParams
 * @version 1.0.1
 * @author wuwg  <wuwg@thunisoft.com>
 * @memberOf   FdGlobal
 * @createTime 2019.08.13
 * @description url
 * @param {String} [url] urlwindow.location.href
 * @returns {object|null} null
 * @update
 * 1.2020-02-10:
 *   1.1 url=   const _urlParams = _url.match(/[?&](.+?=[^&]+)/igm);
 *          const _urlParams = _url.match(/[?&](.+?=[^&]*)/igm);
 *   1.2 url spb[value[0]] = value[1];   spb[decodeURIComponent(value[0])] = decodeURIComponent(value[1]);
 *
 */
function getUrlParams(url) {
    const _url = url || window.location.href;
    const _urlParams = _url.match(/[?&](.+?=[^&]*)/igm);
    // 
    const _defaultParmas = {};
    return _urlParams ? _urlParams.reduce((a, b) => {
        const value = b.slice(1).split('=');
        a[decodeURIComponent(value[0])] = decodeURIComponent(value[1]);
        return a;
    }, _defaultParmas) : null;
}
export default getUrlParams;
