export default {
    methods: {
        // 
        gGownloadTemplate() {
            const id = 'downloadFrame';
            let frame = document.getElementById(id);
            if (!frame) {
                frame = document.createElement('iframe');
                frame.id = id;
                frame.width = '0px';
                frame.height = '0px';
                document.body.appendChild(frame);
            }
            frame.src = window.serverConfig.xzCxdxMb;
        },
        // 
        gGoToFirstErrorElement() {
            // 
            const _elementList = document.querySelectorAll('.error, .invalid, .repeat,.fd-must-set.required');
            // 
            const _element = _elementList[0];
            // 
            const _scrollContain = document.querySelector('.fd-page-spb');
            // 
            if (_element) {
                // 
                window.fdGlobal.setScrollBarPosition(_element, _scrollContain);
            }
        },
        // 
        gGoToFirstCxdxElement() {
            // 
            const _element = document.querySelector('.fd-ac-spb-zrr');
            // 
            const _scrollContain = document.querySelector('.fd-page-spb');
            // 
            if (_element) {
                // 
                window.fdGlobal.setScrollBarPosition(_element, _scrollContain);
            }
        },
        // 
        gShowMessage(info, type = 'error', duration = 5) {
            // 
            this.gHideMessage();
            // 
            this.gMessage = window.Artery.message[type]({
                content: info,
                duration: duration
            });
        },
        // 
        gHideMessage() {
            if (typeof this.gMessage === 'function') {
                this.gMessage();
            }
        },
        // body
        gBodyClickHandler(event) {
            // 
            if (event.target.classList.contains('aty-message')) {
                // 
                this.gHideMessage();
            }
        },
        // 
        gInvalidHandler(message, type = 'error', duration = 5) {
            // 
            this.gShowMessage(message, type, duration);
        }
    }
};

