/**
 *@file request
 *@version 1.0.1
 *@author wanghuifeng
 *@createTime 2020/12/28 - 11:03
 *@updateTime 2020/12/28 - 11:03
 *@see [jsDoc]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description  request 
 @namespace  request   
 
 _name    => 
 _method  
 _showLog  
 _url : url
 _data :   
 _serverData : 

 
 1. fd
 window.fdGlobal.ajax
 2.
 axios

 
 1. 
 window.fdGlobal.consoleLogRequest(_showLog, _name, _method, _url, _data);
 2. 
 window.fdGlobal.consoleLogResponse(_showLog, _name, _serverData);

 important
 promise,
 
 
  index.js 

 request 
 TreeData  
 showLog   index.js  data  {showLog: window.fdConfig.url.showLog}, 


 */
export default {
    methods: {
        // => 
        requestCxxContentListData(queryData) {
            // 
            const _startTime = window.fdGlobal.performance.getCurrentTime();
            return new Promise((resolve, reject) => {
                const _showLog = this.showLog;
                const _name = `=> ${queryData.code}`;
                const _method = window.fdConfig.methodPost;
                let _url = `${window.fdConfig.url.cxx.getContentList}?${new Date().getTime()}`;
                // 
                if (window.fdConfig.isDebug) {
                    _url = `${window.fdConfig.url.cxx.getContentList + queryData.code}.json`;
                }
                // 
                const _data = {
                    // code
                    code: queryData.code,
                    // 
                    cxlx: queryData.cxlx,
                    //  qyCodeType
                    qyCodeType: queryData.qyCodeType
                };
                // 
                window.fdGlobal.consoleLogRequest(_showLog, _name, _method, _url, _data);
                //  shuju
                window.fdGlobal.ajax({
                    method: _method,
                    url: _url,
                    // URL
                    //  URL
                    data: _data,
                    // json
                    responseType: 'json'
                })
                    .then((data) => {
                        const _serverData = data.data;
                        window.fdGlobal.performance.execute(`${_name}ajax  :`, _startTime);
                        // 
                        window.fdGlobal.consoleLogResponse(_showLog, _name, _serverData);
                        resolve(_serverData);
                    }, (data) => {
                        window.fdGlobal.performance.execute(`${_name}ajax  :`, _startTime);
                        const _serverData = data.data;
                        // 
                        window.fdGlobal.consoleLogResponse(_showLog, _name, _serverData);
                        reject(data);
                    });
            });
        }
    }
};
