/**
 *
 * @param {string} value 
 * @param {Boolean} isZhCN 
 * @returns {*} 
 */
export default (value, isZhCN) => {
    if (value === null || value === '' || typeof (value) !== 'string') {
        return '';
    }
    // ie
    let dateStr = value.replace(/\.\d{3}/, '');
    dateStr = dateStr.replace(/-/g, '/');
    const date = new Date(dateStr);
    // 
    const y = date.getFullYear();
    // 
    let m = date.getMonth() + 1;
    m = m < 10 ? `0${m}` : m; // eslint-disable-line
    // 
    let d = date.getDate();
    d = d < 10 ? (`0${d}`) : d; // eslint-disable-line
    return isZhCN ? `${y}${m}${d}` : `${y}-${m}-${d}`;
};
