export default {
    methods: {

        //  ready
        readyCheckedArea(checkedAreaObject) {
            this.jsCheckedArea = checkedAreaObject;
        },

        // 
        updateCheckedArea(dataList) {
            // 
            this.jsCheckedArea.update(dataList);
        },
        // 
        resetCheckedArea() {
            // 
            this.jsCheckedArea.update([]);
        },
        // 
        operateCheckedArea() {
            // 1.
            this.resetCheckedArea();
        },
        // 
        changeCheckedArea(dataList) {
            // 1.
            this.setCheckedAreaList(dataList);
            // 2. 
            this.updateCascadeConnectionChecked(dataList);
        }
    }
};
