/**
 * @namespace  global
 *   global+moduleName +  children moduleName
 *
 * globalEventBus   common 
 *
 */
import Vue from 'vue';
// eventBus
export default new Vue({
    data() {
        return {
            // url
            commonUrlParams: {
                // 
                cBh: '',
                //  19
                nZt: -1,
                // 
                cCxbs:''
            },
            // 
            commonUserInfo: {}
        };
    },
    created() {
        // 
        this.bindGlobalEvent();
    },
    beforeDestroy() {
        // 
        this.unbindGlobalEvent();
    },
    methods: {
        // url
        setCommonUrlParams(commonUrlParams) {
            // url
            this.commonUrlParams = commonUrlParams;
        },
        // 
        setCommonUserInfo(commonUserInfo) {
            // 
            this.commonUserInfo = commonUserInfo;
        },
        // 
        bindGlobalEvent() {
            // 
            this.$on('global-common-urlParams', this.setCommonUrlParams);
            // 
            this.$on('global-common-userInfo', this.setCommonUserInfo);
        },
        // 
        unbindGlobalEvent() {
            // 
            this.$off('global-common-urlParams', this.setCommonUrlParams);
            // 
            this.$off('global-common-userInfo', this.setCommonUserInfo);
        }
    }
});
