// 
export default {
    created() {
        // 
        this.bindGlobalEvent();
    },
    beforeDestroy() {
        // 
        this.unbindGlobalEvent();
    },
    methods: {
        // 
        bindGlobalEvent() {
            //  
            this.globalBus.$on('global-spb-splz-show', this.showSplzPopup);
        },
        // 
        unbindGlobalEvent() {
            //  
            this.globalBus.$off('global-spb-splz-show', this.showSplzPopup);
        },
        // 
        emitGlobalEvent(callback) {
            if (this.globalBus) {
                callback();
            }
        }
    }
};
