import setScrollBarPosition from './setScrollBarPosition.js';

/**
 * @namespace   atc   auto-component
 */
export default {
    props: {
        // 
        localSearch: {
            type: Boolean,
            default: true
        },
        // 
        delay: {
            type: Number,
            default: 200
        }
    },
    data() {
        return {
            // 
            focusing: false,
            // 
            atcActiveIndex: -1,
            // 
            remoting: false
        };
    },
    methods: {
        // 
        setRemoting(flag) {
            this.remoting = flag;
        },
        // 
        setFocusing(flag) {
            this.focusing = flag;
        },
        // 
        hideSearchList() {
            // 1. 
            this.setFocusing(false);
            // 2.
            this.slSetVisible(false);
            // 3.input
            this.$refs.jsInput.blur();
        },
        // index
        atcSetActiveIndex(index) {
            this.atcActiveIndex = index;
        },
        // 
        atcResetActiveItem() {
            // index
            this.atcSetActiveIndex(-1);
        },
        // 
        atcUp() {
            if (!this.slCurrentData.length) {
                return false;
            }
            //  -
            this.atcSetActiveIndex(this.atcActiveIndex - 1);
            //  -
            if (this.atcActiveIndex < 0) {
                // 0 0
                this.atcSetActiveIndex(0);
            }
            // 
            this.atcScrollBarAnimate();
        },

        // 
        atcDown() {
            if (!this.slCurrentData.length) {
                return false;
            }
            //  -
            this.atcSetActiveIndex(this.atcActiveIndex + 1);
            if (this.atcActiveIndex > this.slCurrentData.length - 1) {
                this.atcActiveIndex = this.slCurrentData.length - 1;
            }
            // 
            this.atcScrollBarAnimate();
        },
        // 
        atcScrollBarAnimate() {
            this.$nextTick(() => {
                // 
                const _activeElement = this.$refs[`jsSearch${this.atcActiveIndex}`];
                // 
                if (_activeElement && Array.isArray(_activeElement)) {
                    setScrollBarPosition(_activeElement[0], this.$refs.jsSearchListContain);
                }
            });
        },
        //  
        handleKeyup($event) {
            if (this.slVisible) {
                if (/38|40|13/igm.test($event.keyCode)) {
                    $event.preventDefault();
                }
            }
        },
        // 
        handleKeydown($event) {
            //  38 40  //  13
            switch ($event.keyCode) {
                case 38: // eslint-disable-line
                    if (this.slVisible) {
                        // 38
                        this.atcUp();
                        $event.preventDefault();
                    }
                    break;
                case 40: // eslint-disable-line
                    if (this.slVisible) {
                        // 40
                        this.atcDown();
                        $event.preventDefault();
                    }
                    break;
                case 13: // eslint-disable-line
                    if (this.slVisible) {
                        // 
                        this.atcEnterHandle();
                    }
                    //  
                    // this.hideSearchList();
                    break;
                default :
                    break;
            }
        },
        // 
        handleFocus() {
            // 
            this.setFocusing(true);
            // 
            if (this.currentValue) {
                //   true , 
                this.slSetVisible(true);
                // 
                this.handleInput();
            }
            // focus
            this.$emit('focus', this.currentValue);
        },
        // 
        handleInput() {
            // 
            if (this.currentValue && this.focusing) {
                //     true , 
                this.slSetVisible(true);
                // 
                if (this.inputTimer) {
                    clearTimeout(this.inputTimer);
                }
                // 
                this.inputTimer = setTimeout(() => {
                    // 
                    this.atcResetActiveItem();
                    // 
                    if (this.localSearch) {
                        // 
                        this.slOperateData(this.dataList);
                    } else {
                        // 
                        this.slRemoteSearch(this.currentValue);
                    }
                }, this.delay);
            } else {
                //  false , 
                this.slSetVisible(false);
            }
            // 
            this.$emit('input', this.currentValue);
        },
        // 
        handleBlur() {
            // 1. 
            // this.hideSearchList();
            // 2. 
            // this.atcEnterHandle();
            // 3.blur.
            // this.$emit('blur', this.currentValue);
        },
        // 
        atcEnterHandle() {
            //  -1  &&  
            if (this.atcActiveIndex !== -1 && this.slCurrentData.length) {
                // 
                this.slSelectListItem(this.slCurrentData[this.atcActiveIndex]);
            } else {
                // 
                this.atcSetActiveIndex(-1);
            }
        }
    }
};
