export default {

    methods: {
        //  ready
        readyCascadeConnection(cascadeConnectionObject) {
            this.jsCascadeConnection = cascadeConnectionObject;
        },
        // 
        updateCascadeConnection(dataList, callback) {
            // 1. 
            this.jsCascadeConnection.update(dataList, () => {
                if (typeof callback === 'function') {
                    // 2. 
                    let _checkedDatalist = [];
                    if (this.selectedItem && this.selectedItem.otherProperty) {
                        // 2.1  
                        _checkedDatalist = this.selectedItem.otherProperty.sjyList || [];
                    }
                    // 
                    callback(this.jsCascadeConnection, _checkedDatalist);
                }
            });
        },
        // 
        updateCascadeConnectionChecked(checkedDataList) {
            this.jsCascadeConnection.mWatchValueHandle(checkedDataList);
        },
        // 
        changeCascadeConnection(dataList) {
            if (this.selectedItem.id === '003001002') {
                // eslint-disable-next-line no-param-reassign
                dataList = dataList.filter(item => {
                    return item.name !== '';
                });
            }
            // 1.
            this.setCheckedAreaList(dataList);
            // 2.
            this.updateCheckedArea(dataList);
            // 3. 
            this.operateConnectItemData(dataList);
        },
        // 
        operateConnectItemData(dataList) {
            // 3.2. id
            switch (this.selectedItem.id) {
                // 1.
                // 
                case '003001001':
                    // 
                    this.djxxItemConnectHandler(dataList);
                    break;
                // 
                case '003001002':
                    // 
                    this.hdxxItemConnectHandler(dataList);
                    break;
                default:
                    break;
            }
        },
        // 
        djxxItemConnectHandler(dataList) {
            // 
            // 1.  
            // 2.  
            // 3.  

            // 
            //  === 0, 
            // =>, 
            if (dataList.length === 0 && this.dCurrentCxxDataItem.category !== '05') {
                // 1. 
                const _hdxxNodeItem = this.jsTreeObject.cGetCache('003001002');
                // 
                const _sjyList = _hdxxNodeItem.otherProperty.sjyList;
                //  
                // 
                if (Array.isArray(_sjyList) && _sjyList.length > 0) {
                    // 
                    const _dataList = [];
                    // 
                    this.setCheckedAreaList(_dataList, _hdxxNodeItem);
                }
                // disabled 
                //  
                // ,  contentObject  003001001
                this.updateCascadeConnection(this.contentObject['003001001']);
            }
        },
        // 
        hdxxItemConnectHandler(dataList) {
            // 
            // 1.   
            // 2.  
            // 2.1 
            // 2.2 + 
            // 3.  
            // 
            // 0, 
            // =>, 
            if (dataList.length > 0 && this.dCurrentCxxDataItem.childCategory !== '05') {
                // 1. 
                const _djxxNodeItem = this.jsTreeObject.cGetCache('003001001');
                // 2. 
                const _sjyList = _djxxNodeItem.otherProperty.sjyList;
                // 
                let _dataList = [];
                // 3. 
                // 
                //  0
                if (!Array.isArray(_sjyList) || _sjyList && !this.adjustExist(_sjyList, 'TX000004')) {
                    // 
                    // const _dataList = this.contentObject['003001001'][0].children;
                    // 3.1. 
                    _dataList = JSON.parse(JSON.stringify(dataList));
                } else {
                    // 
                    // 3.2 
                    _dataList = JSON.parse(JSON.stringify(this.contentObject['003001001'][0].children));
                }
                // 4. 
                this.setCheckedAreaList(_dataList, _djxxNodeItem);
            }
        },
        // 
        adjustExist(dataList, id) {
            return dataList.some((item) => item.id === id);
        }
    }
};
