export default {
    // 
    checkLxdh(val) {
        // /  +-
        const reg = /^[0-9]+[/-]?[0-9]*$/;
        if (!val || !(reg.test(val))) {
            return {
                message: '',
                validate: false
            };
        }
        return {
            message: '',
            validate: true
        };
    },
    // 
    checkMobilePhone(phone) {
        // -13
        const reg = /^([\d-\d]{1,13})$/;
        if (!phone || !(reg.test(phone))) {
            return {
                message: '',
                validate: false
            };
        }
        return {
            message: '',
            validate: true
        };
    },
    // 
    checkLicensePlate(code) {
        const reg1 = /^(([][A-Z](([0-9]{5}[DF])|([DF]([A-HJ-NP-Z0-9])[0-9]{4})))|([][A-Z][A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9]))$/;
        if (!code || !(reg1.test(code))) {
            return {
                message: '',
                validate: false
            };
        }
        return {
            message: '',
            validate: true
        };
    },
    // 
    qgdCheck(val) {
        // 6-4-20200909-
        // const reg = /^([1-9]{1})(\d{5})-\d{4}-[1-9]\d{3}(0[1-9]|1[0-2])(0[1-9]|[1-2][0-9]|3[0-1])-\d+$/;
        // 
        const reg = /^[0-9]{6}-[0-9]{4}-[0-9]{8}-[0-9]+(\.[0-9]{n})?/;
        if (!val || !(reg.test(val))) {
            return {
                message: '',
                validate: false
            };
        }
        return {
            message: '',
            validate: true
        };
    },
    // 
    kddhCheck(val) {
        const reg = /^[A-Za-z0-9]{1,20}$/;
        if (!val || !(reg.test(val))) {
            return {
                message: '',
                validate: false
            };
        }
        return {
            message: '',
            validate: true
        };
    },

    /* eslint-disable */
    /**
     *  
     *  1518
     * */
    // (15)
    issfz(value) {
        const cardNumber = value.toLowerCase();
        // 
        if (cardNumber.length !== 18 && cardNumber.length !== 15) {
            return {
                message: '1518',
                validate: false
            };
        }
        const reg = /(^[a-zA-Z0-9]{15}$)|(^[a-zA-Z0-9]{18}$)/;
        if(!reg.test(cardNumber)){
            return {
                message: '1518',
                validate: false
            };
        }
        return {
            message: '',
            validate: true
        };
    },
    /* eslint-enable */
    /**
     *  
     *  5-17
     * */
    // 
    isPassport(value) {
        const c = /^[a-zA-Z0-9]{5,17}$/;
        if (value) {
            if (!c.test(value)) {
                return {
                    message: '',
                    validate: false
                };
            }
        }
        return {
            message: '',
            validate: true
        };
    },
    // 
    isNotHz(value) {
        const reg = new RegExp('[\\u4E00-\\u9FFF]+');
        if (reg.test(value)) {
            return {
                message: '',
                validate: false
            };
        } else {
            return {
                message: '',
                validate: true
            };
        }
    }
};


