/**
 * @namespace sl   search-list  
 *
 */
export default {
    props: {
        // 
        limit: {
            type: Number,
            default: 1000
        },
        // 
        placeholderSearch: {
            type: String,
            default: '...'
        }
    },
    methods: {
        // 
        slSelect(item) {
            const _targetItem = item.item;
            if (_targetItem) {
                // 
                const _pItem = this.cGetCache(_targetItem[this.dpKey]);
                // 
                this.mClickItem(_targetItem, _pItem);
                // 
                this.mAdjustRepeatItem(_targetItem);
            }
        },
        // 
        slAddCurrentDataItem(dataItem) {
            // 
            this.slCurrentDataCache.set(dataItem[this.dKey], dataItem);
        },
        // 
        slSetCurrentData(dataList) {
            this.slCurrentData = dataList;
        },
        // 
        slGetCurrentData() {
            // 
            if (this.slCurrentData.length) {
                return this.slCurrentData;
            }
            // 
            const slCurrentData = [];
            // 
            this.slCurrentDataCache.forEach((item) => {
                if (item.parentId) {
                    slCurrentData.push(item);
                }
            });
            // 
            this.slSetCurrentData(slCurrentData);
            // 
            return slCurrentData;
        },
        // 
        slInitCurrentData() {
            // 
            this.slSetCurrentData([]);
            // 
            this.slCurrentDataCache = new Map();
        },
        // 
        remoteRequestMethod() {
            return new Promise((resolve) => {
                // 
                resolve(this.slGetCurrentData());
            });
        }
    }
};
