export default {
    methods: {
        // 
        eToggleHandler(dataItem) {
            // 1.
            dataItem.extend = !dataItem.extend;
            // 4.
            if (dataItem.extend) {
                this.eToggleExtendHandler(dataItem);
            } else {
                this.eToggleCollpaseHandler(dataItem);
            }
        },

        /**
         * @param {object} dataItem 
         * @description  extend
         * @returns {*} 
         */
        eToggleExtendHandler(dataItem) {
            // 
            this.$emit('extend', dataItem);
        },

        /**
         * @param {object} dataItem 
         * @description  collapse
         * @returns {*} 
         */
        eToggleCollpaseHandler(dataItem) {
            // 
            this.$emit('collapse', dataItem);
        }
    }
};
