const NUM_3 = 3;
export default {
    data() {
        return {
            // 
            popupTitle: '',
            // 
            popupStatus: this.showSplz,
            // 
            paramsElement: null,
            // 
            popupConfirm:{
                show: true,
                text: '',
                callback() {
                    window.console.log('');
                    // popup.hidePopup();
                }
            }
        };
    },
    methods: {
        // 
        setPopupStatus(flag) {
            this.popupStatus = flag;
        },

        // 
        showSplzPopup(paramElement) {
            // 1. 
            this.dOperateInitCysprData();
            // 2. 
            this.dOperateInitSpjlData(paramElement.jbxx);
            // 3. 
            this.dOperateInitMultipleFlag();
            // 4. 
            this.setPopupStatus(true);
            // 5. 
            this.paramsElement = paramElement;
        },

        //  
        clickCancel() {
            // 1. idname
            this.clearSprIdOrName();
            // 2. 
            this.deleteAllSpr();
            // 3.
            this.setPopupStatus(false);
        },
        //  
        clickConfirm() {
            // id
            let sprIds = '';
            this.selectSprList.forEach(function (item, index) { // eslint-disable-line
                sprIds += `${item.code},`;
            });
            if (this.xyhj === 'undefined' || this.xyhj === '') {
                window.Artery.notice.warning({title: ''});
                return;
            } else if (this.xyhj === '1' && (sprIds === 'undefined' || sprIds === '')) {
                window.Artery.notice.warning({title: ''});
                return;
            }

            if (sprIds !== 'undefined' && sprIds.length !== 'undefined' && sprIds !== '') {
                sprIds = sprIds.substring(0, sprIds.length - 1);
            }
            // (12,3:)
            this.dOperateSaveTypeDefaultData(this.queryData(sprIds), NUM_3);
            // 
            this.setPopupStatus(false);
        },
        //  
        queryData(sprIds) {
            this.serverData.sprIds = sprIds;
            // ID
            this.serverData.cxZrrList = this.paramsElement.cxZrrList;
            this.serverData.cxDwList = this.paramsElement.cxDwList;
            this.serverData.cxOtherList = this.paramsElement.cxOtherList;
            this.serverData.jbxx = this.paramsElement.jbxx;
            return JSON.parse(JSON.stringify(this.serverData));
        },
        // idname
        clearSprIdOrName() {
            this.spr.ids = '';
            this.spr.name = '';
        },
        // 
        deleteAllSpr() {
            // 
            this.selectSprList = [];
            // 
            this.cysprList.forEach((value) => {
                value.selected = false;
            });
            // 
            this.selectedNum = 0;
            // 
            this.splzOrganTree = [];
            this.checkedIdsArr = [];
            this.$refs.jsSplzTree.checkedIds = this.checkedIdsArr;
        }
    }
};
