export default {
    methods: {
        // 
        destroyCacheContentData() {
            // 
            if (Object.prototype.toString.call(this.contentObject, null) === '[object Object]') {
                this.contentObject = null;
            }
        },
        // 
        cacheContentData(name, dataList) {
            // 
            if (Object.prototype.toString.call(this.contentObject, null) !== '[object Object]') {
                this.contentObject = {};
            }
            // 
            //    
            this.contentObject[name] = dataList;
        },

        // 
        getContentListDataYys(id) {
            return new Promise((resolve, reject) => {
                // 
                //  
                // 
                Promise.all([
                    // 
                    this.getRemoteContentListData('003001001'),
                    // 
                    this.getRemoteContentListData('003001002')
                ]).then((data) => {
                    //   003001001  , 003001002 ,
                    resolve(id === '003001001' ? data[0] : data[1]);
                }, (data) => {
                    reject(data);
                });
            });
        },
        // 
        getContentListData(id) {
            return new Promise((resolve, reject) => {
                // 
                // if (this.contentObject && Array.isArray(this.contentObject[id])) {
                // resolve(this.contentObject[id]);
                // } else {
                // 
                this.getRemoteContentListData(id).then((data) => {
                    resolve(data);
                }, (data) => {
                    reject(data);
                });
                // }
            });
        },
        // 
        getRemoteContentListData(id) {
            return new Promise((resolve, reject) => {
                // 
                const _targetItem = this.jsTreeObject.cGetCache(id);
                // 
                if (_targetItem) {
                    // id
                    const _code = _targetItem.id;
                    //  = 01 / 02  (/0405)
                    const _cxlx = (/^01|02$/igm).test(this.dCurrentCxxDataItem.category) ? this.dCurrentCxxDataItem.category : this.dCurrentCxxDataItem.childCategory;
                    //  qyCodeType
                    const _qyCodeType = _targetItem.qyCodeType;
                    // 
                    const queryData = {
                        // code
                        code: _code,
                        // 
                        cxlx: _cxlx,
                        //  qyCodeType
                        qyCodeType: _qyCodeType
                    };
                    // => 
                    this.requestCxxContentListData(queryData).then((serverData) => {
                        // 
                        this.cacheContentData(queryData.code, serverData.data);
                        // 
                        resolve(serverData.data);
                    }, (data) => {
                        // 
                        reject(data);
                    });
                } else {
                    // 
                    reject(new Error(`${id}`));
                }
            });
        },
        // 
        watchSelectItem(selectItem, asideTree, dCurrentCxxDataItem) {
            // 
            this.jsTreeObject = asideTree;
            // 
            this.dCurrentCxxDataItem = dCurrentCxxDataItem;
            // important
            // 1. 
            this.setSelectedItem(selectItem);
            // 2. 
            this.operateDateGroup(selectItem);
            // 3. 
            this.operateEmptyContent(selectItem);
            // 3. 
            if (!this.emptyContent) {
                // 3.1. 
                this.operateCheckedArea();
                // 3.2. id
                switch (selectItem.id) {
                    // 1.
                    // 
                    case '001001001':
                        // []
                        this.zhxxItemHandler(selectItem.id);
                        break;
                    // 
                    case '001001002':
                        this.yjlsItemHandler(selectItem.id);
                        break;
                    // 
                    case '001001003':
                        this.jrlcItemHandler(selectItem.id);
                        break;
                    // 2.
                    // 
                    case '003001001':
                        this.djxxItemHandler(selectItem.id);
                        break;
                    // 
                    case '003001002':
                        this.hdxxItemHandler(selectItem.id);
                        break;
                    // 3.
                    // 
                    case '004002002':
                        this.bdcjzcxItemHandler(selectItem.id);
                        break;
                    default:
                        break;
                }
            }
        }
    }
};
