/**
 * @namespace mul   multiple  
 *
 */
// 
import cache from './cache.js';
// 
export default {
    mixins: [cache],
    methods: {
        // 
        mInitCheckedData() {
            // 
            this.mSetCurrentValue([]);
            // 
            this.mSetCheckedData([]);
            // 
            this.mCheckedCache = new Map();
        },
        // 
        mSetCurrentValue(value) {
            this.mCurrentValue = value;
        },
        // 
        mSetCheckedData(value) {
            this.mCheckedData = value;
        },
        // 
        mResetCheckedData() {
            // 1.false
            this.mCheckedCache.forEach((item) => {
                if (item) {
                    // 
                    item.checked = false;
                    // 
                    if (item.hasOwnProperty('otherProperty')) {
                        item.otherProperty = {};
                    }
                }
            });
            // 2.
            this.mCheckedCache.clear();
            // 3.
            this.mSetCurrentValue([]);
            // 4.
            this.mSetCheckedData([]);
        },
        //  ,  forceUpdate = true 
        mWatchValueHandle(newValue, forceUpdate = false) {
            // 
            const _value = this.mBeforeAdjustHandle(newValue);
            // 
            if (_value.toString() !== this.mCurrentValue.toString() || forceUpdate) {
                // 1. 
                this.mResetCheckedData();
                // 2. 
                this.mSetCurrentValue(_value);
                // 3. 
                this.mOperateCheckedData(newValue);
                // 4.
                this.mEmitEvent();
            }
        },
        // 
        mBeforeAdjustHandle(newValue) {
            // 
            if (!this.mCurrentValue) {
                // 
                this.mInitCheckedData();
            }
            //  value
            let _value = newValue;
            // 
            if (!Array.isArray(_value)) {
                _value = typeof _value === 'string' ? [_value] : [];
            }
            // 0
            if (Array.isArray(_value) && _value.length > 0 && typeof _value[0] === 'object') {
                _value = _value.map((item) => item[this.dKey]);
            }
            return _value;
        },

        // 
        mOperateCheckedData(newValue) {
            // const _dataList = [];
            // 
            // this.cGetAllCache()
            //     .forEach((item) => {
            //         item.checked = false;
            //     });
            // 
            newValue.forEach((item) => {
                // 
                const _dataItem = this.cGetCache(item[this.dKey]);
                // 
                if (_dataItem) {
                    _dataItem.checked = true;
                    // 
                    if (_dataItem.hasOwnProperty('otherProperty')) {
                        // 
                        if (item.hasOwnProperty('sjyList')) {
                            _dataItem.otherProperty.sjyList = item.sjyList;
                        }
                        // 
                        if (item.hasOwnProperty('startDate')) {
                            _dataItem.otherProperty.startDate = item.startDate;
                        }
                        // 
                        if (item.hasOwnProperty('endDate')) {
                            _dataItem.otherProperty.endDate = item.endDate;
                        }
                        if (item.hasOwnProperty('type')) {
                            _dataItem.otherProperty.type = item.type;
                        }
                    }
                    // 
                    this.mAddCheckedItem(_dataItem, false);
                }
            });
        },
        //  @todo  
        mUpdateItemCheckedStatus(item) {
            // 
            const _pItem = this.cGetCache(item[this.dpKey]) || this.currentNavList;
            // 1.0
            if (item.checked) {
                // 
                this.mAddCheckedItem(item);
            } else {
                // 
                this.mDeleteCheckedItem(item);
            }
            // 2.
            _pItem[this.dChildren].splice(0, 0);
        },
        // 
        mClickItem(item, pItem) {
            // 
            // @todo   
            if ((!item.hasExtendArrow && this.checkedHandler(item, pItem, this)) || (item.id == '002002001') || (item.id == '001002002')) {
                // 
                item.checked = !item.checked;
                // 
                if (item.checked) {
                    // 
                    this.mAddCheckedItem(item);
                    // 
                } else {
                    // 
                    this.mDeleteCheckedItem(item);
                }
                // 
                pItem[this.dChildren].splice(0, 0);
            }
        },
        // , boolean
        mAddCheckedItem(item, emitEvent = true) {
            // 1. 
            this.mCheckedCache.set(item[this.dKey], item);
            // 2.
            if (emitEvent) {
                // 
                this.mEmitEvent();
            }
        },
        // 
        mDeleteCheckedItem(item, emitEvent = true) {
            // 1.
            this.mCheckedCache.delete(item[this.dKey]);
            // 2.
            if (emitEvent) {
                // 
                this.mEmitEvent();
            }
        },
        // 
        mSetCurrentCheckedData() {
            // 
            const mCurrentValue = [];
            // 
            const mCheckedData = [];
            // Map
            this.mCheckedCache.forEach((item, key) => {
                // 
                mCurrentValue.push(key);
                // 
                mCheckedData.push(item);
            });
            // 1.
            this.mSetCurrentValue(mCurrentValue);
            // 2.
            this.mSetCheckedData(mCheckedData);
        },
        // 
        mEmitEvent() {
            // 1.
            this.mSetCurrentCheckedData();
            // 2.
            this.$emit('input', this.mCurrentValue);
            // change 
            this.$emit('change', this.mCheckedData);
        }
    }
};
