/**
 *@file console
 *@version 1.0.1
 *@author wuwg
 *@createTime 2019/8/25 - 16:47
 *@updateTime 2019/8/25 - 16:47
 *@see [jsDoc]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description console consoleLogRequestconsoleLogResponse consoleLogError consoleLog
 */
export default {

    /**
     * @namejsdoc
     * @function
     * @version 1.0.1
     * @author wuwg  <wuwg@thunisoft.com>
     * @alias FdGlobal.consoleLogRequest
     * @description  request()
     * @time 2018-08-25
     * @param {boolean} showLog 
     * @param {string} name  
     * @param {string} method    post,getdelete.etc,
     * @param {string} url url
     * @param {object} requestData 
     * @returns {undefined} 
     */
    consoleLogRequest(showLog, name, method, url, requestData) {
        if (showLog) {
            window.console.log(`[[]]==>${name}`);
            window.console.log('[[]]==>');
            window.console.log(method);
            window.console.log('[[]]==>url');
            window.console.log(url);
            window.console.log('[[]]==>');
            window.console.log(requestData);
            window.console.log('\n');
        }
    },

    /**
     * @namejsdoc
     * @function
     * @version 1.0.1
     * @author wuwg  <wuwg@thunisoft.com>
     * @alias FdGlobal.consoleLogResponse
     * @description  response()
     * @time   2018-08-25
     * @param {boolean} showLog 
     * @param {string} name 
     * @param {object} responseData  
     * @returns {undefined} 
     */
    consoleLogResponse(showLog, name, responseData) {
        if (showLog) {
            window.console.log(`%c==>${name}`, 'color:#00cc66');
            window.console.log('%c==>', 'color:#00cc66');
            window.console.dir(responseData);
            window.console.log('\n');
            window.console.log('\n');
        }
    },

    /**
     * @namejsdoc
     * @function
     * @version 1.0.1
     * @author wuwg  <wuwg@thunisoft.com>
     * @alias FdGlobal.requestError
     * @description  error()
     * @time   2018-08-25
     * @param {object} name  
     * @param {object} data  
     * @param  {number} textStatus 
     * @param  {object} errorThrown 
     * @returns {undefined} 
     */
    consoleLogError(name, data, textStatus, errorThrown) {
        // 2018-03-21 ie try{}catch(e){}
        try {
            window.console.log(`[[]]==>${name}`);
            window.console.error('');
            window.console.error(data);
            if (textStatus) {
                window.console.error(textStatus);
            }
            if (errorThrown) {
                window.console.error(errorThrown);
            }
        } catch (e) {
            // console.log(e)
        }
    },

    /**
     * @namejsdoc
     * @function
     * @version 1.0.1
     * @author wuwg  <wuwg@thunisoft.com>
     * @alias FdGlobal.consoleLog
     * @description  log
     * @time 2018-03-21
     * @param {boolean} showLog   
     * @param {*}  logInfo  ,
     *  @returns {undefined} 
     */
    consoleLog(showLog, logInfo) {
        if (showLog) {
            window.console.log(logInfo);
        }
    }
};
