export default {
    data() {
        return {};
    },
    methods: {
        // 
        yhjrjgHandler(id) {
            // 
            this.getContentListData(id)
                .then((dataList) => {
                    // 
                    this.updateCascadeConnection(dataList, (jsCascadeConnection, _checkedDatalist) => {
                        // 2.1  
                        if (_checkedDatalist.length === 0) {
                            // 
                            // const _targetItem = jsCascadeConnection.cGetCache('1');
                            // 
                            // jsCascadeConnection.clickSelectAll(_targetItem);
                            // 2.2  
                        } else {
                            // 3. 
                            jsCascadeConnection.mWatchValueHandle(_checkedDatalist);
                        }
                    });
                });
        },
        // 1.
        // 
        zhxxItemHandler(id) {
            // 1. 
            this.yhjrjgHandler(id);
        },
        // 
        yjlsItemHandler(id) {
            // 1. 
            this.yhjrjgHandler(id);
        },
        // 
        jrlcItemHandler(id) {
            this.yhjrjgHandler(id);
        },
        // 
        yysHandler(id) {
            //  
            this.getContentListDataYys(id)
                .then((dataList) => {
                    if (id === '003001002') {
                        dataList.forEach(item => {
                            item.children = item.children.filter(xcdw => {
                                return xcdw.name !== '';
                            });
                        });
                    }
                    // 
                    const _dataList = id === '003001002' ? this.transformHdxxList(dataList) : this.transformDjxxList(dataList);
                    // 
                    this.updateCascadeConnection(_dataList, (jsCascadeConnection, _checkedDatalist) => {
                        // 2.1  
                        if (_checkedDatalist.length === 0) {
                            // 
                            const _targetItem = jsCascadeConnection.cGetCache('1');
                            // 
                            jsCascadeConnection.clickSelectAll(_targetItem);
                            // 2.2  
                        } else {
                            // 3. 
                            jsCascadeConnection.mWatchValueHandle(_checkedDatalist);
                        }
                    });
                });
        },
        // 
        // 1. disabled 
        // 2.  disabled 
        //  
        transformDjxxList(dataList) {
            // 1. 
            const _hdxxNodeItem = this.jsTreeObject.cGetCache('003001002');
            // 
            const _sjyList = _hdxxNodeItem.otherProperty.sjyList;
            let _dataList = dataList;
            // 0 
            //  =>, 
            if (Array.isArray(_sjyList) && _sjyList.length > 0 && this.dCurrentCxxDataItem.childCategory !== '05') {
                // keys
                const _sjyListKeys = _sjyList.map((item) => item.id);
                // 
                _dataList = JSON.parse(JSON.stringify(dataList));
                if (Array.isArray(_dataList) && _dataList.length) {
                    _dataList[0].children.forEach((item) => {
                        if (_sjyListKeys.indexOf(item.id) !== -1) {
                            // 
                            item.disabled = true;
                        }
                    });
                }
            }
            return _dataList;
        },
        // 
        //  disabled 
        transformHdxxList(dataList) {
            const _dataList = JSON.parse(JSON.stringify(dataList));
            if (Array.isArray(_dataList) && _dataList.length) {
                _dataList[0].children.forEach((item) => {
                    // 
                    item.disabled = true;
                });
            }
            return _dataList;
        },
        // 2.,
        // 2.1 
        djxxItemHandler(id) {
            // 
            this.yysHandler(id);
        },
        // 2.2 
        hdxxItemHandler(id) {
            //  214 4
            // 
            this.yysHandler(id);
        },
        // 3.
        // 
        bdcjzcxItemHandler(id) {
            // 
            this.getContentListData(id)
                .then((dataList) => {
                    // 
                    this.updateCascadeConnection(dataList, (jsCascadeConnection, _checkedDatalist) => {
                        // 
                        if (_checkedDatalist.length !== 0) {
                            // 3. 
                            jsCascadeConnection.mWatchValueHandle(_checkedDatalist);
                        }
                    });
                });
        },
        // 4.
        //  => 
        zqcyItemHandler(selectedItem) {
            // 
            if (!selectedItem.checked) {
                // , 
                this.updateDateGroup(Date.now() - 86400000, ''); // eslint-disable-line
            }
        },
        //  =>  
        zqcybdItemHandler(selectedItem) {
            // 
            if (!selectedItem.checked) {
                // 102021.1.152011.1.15
                const currentStartDate = new Date().setFullYear(new Date().getFullYear() - 10); // eslint-disable-line
                //  
                this.updateDateGroup(currentStartDate, Date.now());
            }
        }
    }
};
