        filterDataList(dataList) {
            const _dataList = [];
            // 
            this.filterControl(parseInt(this.filterYwActiveItem.category, 10));
            // 
            const _filterPKeys = Object.keys(this.filterShowKeysObject);
            // 
            dataList.forEach((item) => {
                // 
                // if (this.filterKeys.indexOf(item[this.dKey]) !== -1) {
                if (_filterPKeys.indexOf(item[this.dKey]) !== -1) {
                    // 
                    const _targetItem = {};
                    // 
                    Object.keys(item).forEach((key) => {
                        if (key === 'children') {
                            _targetItem[key] = [];
                        } else {
                            _targetItem[key] = item[key];
                        }
                    });
                    // 
                    _dataList.push(_targetItem);
                    // 
                    const _childrenKeys = this.filterShowKeysObject[item[this.dKey]];
                    // 
                    if (_childrenKeys.length && item.children.length) {
                        // 
                        item.children.forEach((itemC) => {
                            // 
                            if (_childrenKeys.indexOf(itemC[this.dKey]) !== -1) {
                                // 
                                const _targetItemC = {};
                                // 
                                Object.keys(itemC).forEach((key) => {
                                    if (key === 'children') {
                                        _targetItemC[key] = [];
                                    } else {
                                        _targetItemC[key] = itemC[key];
                                    }
                                });
                                // 
                                _targetItem.children.push(_targetItemC);
                            }
                        });
                    }
                }
            });
            // children
            for (let i = _dataList.length - 1; i >= 0; i--) {
                if (_dataList[i]['children'].length === 0) {
                    _dataList.splice(i, 1);
                }
            }
            return _dataList;
        },
