export default {
    data() {
        return {
            // 
            showEndDate: true,
            // 
            showDateGroup: false,
            // 
            startDate: Date.now(),
            // 
            // 86400000  = 24*60*60*1000
            endDate: Date.now() + 86400000,
            // 
            startDateLabel: '',
            // 
            endDateLabel: ''
        };
    },
    methods: {
        //  ready
        readyDateGroup(dateGroupObject) {
            this.jsDateGroup = dateGroupObject;
        },
        // 
        setShowDateGroupStatus(flag) {
            this.showDateGroup = flag;
        },
        // 
        updateDateGroup(startDate, endDate) {
            this.jsDateGroup.update(startDate, endDate);
        },
        // 
        resetDateGroup() {
            this.updateDateGroup('', '');
        },
        // 
        operateDateGroup(selectedItem) {
            // => 001001002
            // => 001003002
            // => 001003003
            // => 001002002
            const flag = (/^(001001002|001003002|001003003|001002002)$/igm).test(selectedItem.id);
            // 1. 
            // 
            this.setShowDateGroupStatus(flag);
            // 
            if (flag) {
                // 2.
                this.resetDateGroup();
                // 3. 
                if (selectedItem.otherProperty) {
                    const startDate = selectedItem.otherProperty.startDate || '';
                    const endDate = selectedItem.otherProperty.endDate || '';
                    // 
                    this.updateDateGroup(startDate, endDate);
                }
                // 4.
                this.setDateLabel(selectedItem.id);
            }
        },
        // 
        setShowEndDate(flag) {
            this.showEndDate = flag;
        },
        // 
        setDateLabel(id) {
            // 
            this.setShowEndDate(true);
            // id
            switch (id) {
                // => 001001002
                case '001001002':
                    // 
                    this.startDateLabel = '';
                    // 
                    this.endDateLabel = '';
                    break;
                // => 001003002
                case '001003002':
                    // 
                    this.startDateLabel = '<em class = "required">*</em>';
                    //  
                    this.setShowEndDate(false);
                    break;
                // => 001003003
                case '001003003':
                    // 
                    this.startDateLabel = '<em class = "required">*</em>';
                    // 
                    this.endDateLabel = '';
                    break;
                // => 001002002
                case '001002002':
                    // 
                    this.startDateLabel = '';
                    // 
                    this.endDateLabel = '';
                    break;
                default:
                    break;
            }
        },
        // 
        changeDate(startDate, endDate) {
            // 
            this.startDate = startDate;
            // 
            this.selectedItem.otherProperty.startDate = startDate;
            // 
            if (typeof endDate === 'string') {
                // 
                this.endDate = endDate;
                // 
                this.selectedItem.otherProperty.endDate = endDate;
            }
            switch (this.selectedItem.id) {
                // ===
                case '001001002':
                    // 1. 
                    // 2. 
                    break;
                // ===
                case '001003003':
                    // 
                    // , 
                    // 

                    break;
                // ===
                case '001002002':
                    // 1. 
                    // 2. 
                    break;
                default:
                    // 
                    //  this.selectedItem.checked = Boolean(this.startDate);
                    break;
            }
            // 
            // this.jsTreeObject.mUpdateItemCheckedStatus(this.selectedItem);
        },
        tipsHandler(info) {
            this.emitGlobalEvent(() => {
                // 
                this.globalBus.$emit('global-spb-invalid', info, 'warning');
            });
        }
    }
};
