import toggle from './toggle.js';
//  
export default {
    // 
    mixins: [toggle],
    data() {
        return {
            // 
            currentNavList: [],
            // 
            activeItem: {}
        };
    },
    methods: {
        // 
        setCurrentNavList(dataList) {
            this.currentNavList = dataList;
            if (this.currentNavList.length === 0) {
                this.globalBus.$emit('global-spb-lefttree-cxx', true);
            } else {
                this.globalBus.$emit('global-spb-lefttree-cxx', false);
            }
        },
        // 
        initNavList(dataList) {
            // @todo 
            const _dataList = this.filterDataList(dataList);
            // 
            this.transformDataList(_dataList);
            // 
            this.setCurrentNavList(_dataList);
        },
        // 
        clickItem(item, pItem) {
            // 
            if (this.beforeClickHander(item, this)) {
                // 1.
                if (this.canSelectHandler(item) && !item.disabled) {
                    // 
                    this.setActiveItem(item);
                }
                // 2.
                if (item.leaf) {
                    this.eToggleHandler(item);
                }
                // 3.
                if (item.multiple) {
                    // 
                    this.mClickItem(item, pItem);
                }
                // 4.
                this.$emit('click', item);
            }
        },
        // 
        setActiveItem(item) {
            // 
            this.activeItem = item;
            //  select
            this.$emit('select', item);
        },

        /**
         *
         * @param {Array} dataList 
         * @returns {void} 
         */
        transformDataList(dataList) {
            // 
            this.cInitCache();
            // 
            dataList.forEach((item) => {
                // 
                this.singleRecursion(item, null);
            });
        },

        /**
         * 
         * @param {Object} dataItem 
         * @param {Object} dataItemP 
         * @returns {void} 
         */
        singleRecursion(dataItem, dataItemP) {
            // 1. 
            this.dAddProperty(dataItem, dataItemP);
            // 2. 
            if (dataItem[this.dChildren].length) {
                // 
                dataItem[this.dChildren].forEach((itemC) => {
                    // 
                    this.singleRecursion(itemC, dataItem);
                });
            }
            //  3.
            this.cAddCache(dataItem[this.dKey], dataItem);
        },
        // 
        dAddProperty(dataItem, dataItemP) {
            // 1. children, 
            if (!Array.isArray(dataItem[this.dChildren])) {
                dataItem[this.dChildren] = [];
            }
            // 2. checked checked
            dataItem.checked = false;
            // 3.multiple  multiple 
            if (this.addMultipleProperty(dataItem, dataItemP, this)) {
                // 3.1 
                dataItem.multiple = true;
            }
            // 4. hasExtendArrow
            dataItem.hasExtendArrow = this.hasExtendArrowHandler(dataItem);
            // 5.leaf 
            dataItem.leaf = dataItem[this.dChildren].length > 0;
            // 6.extend 
            dataItem.extend = typeof (dataItem.extend) === 'boolean' ? dataItem.extend : true;
            // 7.otherProperty  , @todo 
            if (dataItem.hasExtendArrow) {
                // 7.1 checkedDatalist  0 
                // 7.2 startDate  
                // 7.3 endDate  
                dataItem.otherProperty = {};
            }
        }
    }
};
