export default {
    methods: {
        // 
        cInitCache() {
            this.cResetCache();
        },
        // 
        cDestroyCache() {
            this.cacheObject = null;
        },
        cResetCache() {
            this.cacheObject = new Map();
        },
        // 
        cAddCache(key, data) {
            this.cacheObject.set(key, data);
        },
        // 
        cGetCache(key) {
            return this.cacheObject.get(key);
        },
        // key:value
        cGetAllCache() {
            return this.cacheObject;
        },
        // 
        cDeleteCache(key) {
            return this.cacheObject.delete(key);
        }
    }
};
