/**
 * @namespace mul   multiple  
 *
 */
// 
import cache from './cache.js';
// 
export default {
    mixins: [cache],
    methods: {
        // 
        mInitCheckedData() {
            // 
            this.mSetCurrentValue([]);
            // 
            this.mSetCheckedData([]);
            // 
            this.mCheckedCache = new Map();
        },
        // 
        mSetCurrentValue(value) {
            this.mCurrentValue = value;
        },
        // 
        mSetCheckedData(value) {
            this.mCheckedData = value;
        },
        // 
        mResetCheckedData() {
            // 1.false
            this.mCheckedCache.forEach((item) => {
                // 
                if (item) {
                    // 1.1.
                    item.checked = false;
                    // 1.2.
                    if (item.repeatCache) {
                        // 
                        const _repeatItem = this.cGetCache(item.repeatCacheId);
                        if (_repeatItem) {
                            _repeatItem.checked = false;
                        }
                    }
                    // 1.3.
                    this.mDeleteCheckedItem(item, false);
                }
            });
            // 2.
            this.mInitCheckedData();
        },
        //  ,  forceUpdate = true 
        mWatchValueHandle(newValue, forceUpdate = false) {
            // 
            const _value = this.mBeforeAdjustHandle(newValue);
            // 
            if (_value.toString() !== this.mCurrentValue.toString() || forceUpdate) {
                // 1. 
                this.mResetCheckedData();
                // 2. 
                this.mSetCurrentValue(_value);
                // 3. 
                this.mOperateCheckedData();
                // 4.
                this.mEmitEvent();
            }
        },
        // 
        mBeforeAdjustHandle(newValue) {
            // 
            if (!this.mCurrentValue) {
                // 
                this.mInitCheckedData();
            }
            //  value
            let _value = newValue;
            // 
            if (!Array.isArray(_value)) {
                _value = typeof _value === 'string' ? [_value] : [];
            }
            // 0
            if (Array.isArray(_value) && _value.length > 0 && typeof _value[0] === 'object') {
                _value = _value.map((item) => item[this.dKey]);
            }
            return _value;
        },

        // 
        mOperateCheckedData() {
            // const _dataList = [];
            // 
            // this.cGetAllCache()
            //     .forEach((item) => {
            //         item.checked = false;
            //     });
            // 1.
            this.mCurrentValue.forEach((item) => {
                // 
                const _dataItem = this.cGetCache(item);
                // 
                if (_dataItem) {
                    _dataItem.checked = true;
                    // 
                    if (item.repeatCache) {
                        // 
                        const _repeatItem = this.cGetCache(item.repeatCacheId);
                        // 
                        if (_repeatItem) {
                            _repeatItem.checked = true;
                        }
                    }
                    // 
                    this.mAddCheckedItem(_dataItem, false);
                }
            });
            // 2. 
            this.mCheckedCache.forEach((item) => {
                // 
                this.operateParentCheckedStatus(item);
            });
        },

        // 
        mAdjustRepeatItem(item) {
            // 
            if (item.repeatCache) {
                // 
                const _repeatItem = this.cGetCache(item.repeatCacheId);
                // 
                const _pRepeatItem = this.cGetCache(_repeatItem[this.dpKey]);
                // 
                this.mClickItem(_repeatItem, _pRepeatItem);
            }
        },
        // 
        mClickItem(item, pItem, maxcount) {
            // 
            item.checked = !item.checked;
            // 
            if (item.checked) {
                // 
                this.mAddCheckedItem(item, maxcount);
                // 
            } else {
                // 
                this.mDeleteCheckedItem(item);
            }
            // 
            pItem[this.dChildren].splice(0, 0);
            // 
            this.$emit('clickItem', item);
        },

        // 
        operateParentCheckedStatus(dataItem) {
            // 
            const _pDataItem = this.cGetCache(dataItem[this.dpKey]);
            // 
            if (_pDataItem && _pDataItem.multiple) {
                // , 
                if (dataItem.checked) {
                    // 
                    _pDataItem.checked = _pDataItem[this.dChildren].every((item) => item.checked);
                }
                // , 
                if (!dataItem.checked && _pDataItem.checked) {
                    _pDataItem.checked = false;
                }
            }
        },
        // 
        mClearHandle(emitEvent = true) {
            // 
            this.mCheckedCache.clear();
            // 
            if (emitEvent) {
                // 
                this.mEmitEvent();
            }
        },
        // , boolean
        mAddCheckedItem(item, maxCount, emitEvent = true, adjustParentCheckedStatus = true) {
            // 1. 
            if (this.mCheckedCache.has(item[this.dKey])) {
                // 1.1 
                if (adjustParentCheckedStatus) {
                    // 
                    this.operateParentCheckedStatus(item);
                }
                // 1.2  
                return true;
            }
            // 2.  
            if (maxCount) {
                this.selectMaxCount = maxCount;
            }
            if (maxCount && this.mCheckedCache.size + 1 > this.selectMaxCount) {
                if (typeof this.tipsHandler === 'function') {
                    // 
                    this.tipsHandler(`${this.selectMaxCount}`);
                }
                this.$emit('error', `${this.selectMaxCount}`);
                // 
                item.checked = false;
                //  
                return false;
            }
            // 3. 
            if (this.mCheckedCache.size < this.selectMaxCount) {
                this.mCheckedCache.set(item[this.dKey], item);
            }
            // 4.
            if (emitEvent) {
                // 
                this.mEmitEvent();
            }
            //  
            return true;
        },
        // 
        mDeleteCheckedItem(item, emitEvent = true, adjustParentCheckedStatus = true) {
            // 1. 
            if (adjustParentCheckedStatus) {
                // 
                this.operateParentCheckedStatus(item);
            }
            // 2.
            this.mCheckedCache.delete(item[this.dKey]);
            // 3.
            if (emitEvent) {
                // 
                this.mEmitEvent();
            }
        },
        // 
        mSetCurrentCheckedData() {
            // 
            const mCurrentValue = [];
            // 
            const mCheckedData = [];
            // Map
            this.mCheckedCache.forEach((item, key) => {
                // 
                mCurrentValue.push(key);
                // 
                mCheckedData.push(item);
            });
            // 1.
            this.mSetCurrentValue(mCurrentValue);
            // 2.
            this.mSetCheckedData(mCheckedData);
        },
        // 
        mEmitEvent() {
            // 1.
            this.mSetCurrentCheckedData();
            // 2.
            this.$emit('select', this.mCurrentValue);
            // change 
            this.$emit('change', this.mCheckedData);
        }
    }
};
