export default {
    data() {
        return {
            // 
            popupTitle: '',
            // 
            popupStatus: this.showDrcxdx,
            // 
            elementParamsCbh: '',
            //  +  + 
            addCountTotal: 0
        };
    },
    methods: {
        // 
        setPopupStatus(flag) {
            this.popupStatus = flag;
        },

        // 
        showDrcxdxPopup(cBh) {
            // 1. 
            this.elementParamsCbh = cBh;
            // 2.  +  + 
            this.addCountTotal = this.$parent.jsZrr.operateOriginDataList().length + this.$parent.jsJg.operateOriginDataList().length + this.$parent.jsQt.operateOriginDataList().length;
            // 3. 
            this.setPopupStatus(true);
        },

        //  
        clickCancel() {
            // 
            this.clearUploadFiles();
            // 2.
            this.setPopupStatus(false);
        },
        // 
        clickConfirm() {
            // if (this.$refs.uploadBdcdxEle.fileList === null || this.$refs.uploadBdcdxEle.fileList.length === 0) {
            if (this.files.length === 0) {
                // window.Artery.notice.info({title: ''});
                // 
                this.globalBus.$emit('global-spb-invalid', '', 'error');
                return false;
            }
            const _params = {
                // 
                jbxxBh: this.elementParamsCbh,
                key: this.uploadBdcdxKey
            };
            // 
            this.requestConfirmDrcxdx(_params)
                .then((data) => {
                    this.operateConfirmData(data);
                }, (data) => {
                    window.console.error(data);
                });
        },
        // 
        operateConfirmData(data) {
            if (data.success) {
                // 
                const cxZrrList = data.data.cxZrrList;
                // /
                const cxDwList = data.data.cxDwList;
                // 
                const cxOtherList = data.data.cxOtherList;
                // 
                const jbxx = data.data.jbxx;
                this.emitGlobalEvent(() => {
                    // 
                    this.globalBus.$emit('global-spb-zrr-setQueryData', cxZrrList);
                    this.globalBus.$emit('global-spb-jg-setQueryData', cxDwList);
                    this.globalBus.$emit('global-spb-qt-setQueryData', cxOtherList);
                    // 
                    if (jbxx) {
                        this.globalBus.$emit('global-spb-jbxx-setQueryData', jbxx);
                    }
                });
                //  
                this.clickCancel();
            } else {
                // 
                this.globalBus.$emit('global-spb-invalid', data.message, 'error');
            }
        }
    }
};
