// 
import fdRequest from './fdRequest.js';
// url
import getUrlParams from '@/components/tools/extend/getUrlParams.js';
// eventBus
import globalBus from '@/components/globalBus/index.js';

// 
export default {
    // url
    operateUrlParams() {
        return new Promise((resolve) => {
            const commonUrlParams = getUrlParams() || {cBh: '', nZt: -1, cCxbs:''};
            globalBus.$emit('global-common-urlParams', commonUrlParams);
            resolve(commonUrlParams);
        });
    },
    // 
    adjustLogined() {
        return new Promise((resolve, reject) => {
            // 
            fdRequest.requestAdjustLogin()
                .then((data) => {
                    // 
                    resolve(data);
                }, (data) => {
                    // 
                    if (typeof data.data.data !== 'undefined' && data.data.data.gw_code === 401) {
                        const login = 'login';
                        window.top.location.href = `${window.origin}${login}${encodeURIComponent(window.top.location)}`;
                    } else {
                        window.top.location.href = `${window.location.origin + window.projectName}/page/500/index.html`;
                    }
                    // 
                    reject(data);
                });
        });
    },

    // 
    operateCode() {
        return new Promise((resolve, reject) => {
            // 
            fdRequest.requestCode()
                .then((serverData) => {
                    // /==
                    const _gbglqxList = serverData.data.gbglqxList;
                    const _gbglqxCodeType = _gbglqxList[0].codeType;
                    // /==
                    const _zjList = serverData.data.zjList;
                    const _zjCodeType = _zjList[0].codeType;
                    // ==
                    const _syList = serverData.data.cxsyList;
                    const _syCodeType = _syList[0].codeType;
                    // == 
                    const _cxlxList = serverData.data.cxdxlxList;
                    const _cxlxCodeType = _cxlxList[0].codeType;
                    // == 
                    const _zjlxList = serverData.data.zjlxList;
                    const _zjlxCodeType = _zjlxList[0].codeType;
                    // /== 
                    const _zzlxList = serverData.data.zzlxList;
                    const _zzlxCodeType = _zzlxList[0].codeType;
                    // @todo wanghuifeng codeType  20210130
                    // /==
                    window.fdDataTable.gbglqxCodeType = _gbglqxCodeType;
                    // /==
                    window.fdDataTable.zJCodeType = _zjCodeType;
                    // ==
                    window.fdDataTable.syCodeType = _syCodeType;
                    // == 
                    window.fdDataTable.cxlxCodeType = _cxlxCodeType;
                    // == 
                    window.fdDataTable.zjlxCodeType = _zjlxCodeType;
                    // /== 
                    window.fdDataTable.zzlxCodeType = _zzlxCodeType;
                    // @todo wanghuifeng   20210130
                    // /==
                    window.fdDataTable.setCodeTypeData(_gbglqxCodeType, _gbglqxList);
                    // /==
                    window.fdDataTable.setCodeTypeData(_zjCodeType, _zjList);
                    // ==
                    window.fdDataTable.setCodeTypeData(_syCodeType, _syList);
                    // == 
                    window.fdDataTable.setCodeTypeData(_cxlxCodeType, _cxlxList);
                    // == 
                    window.fdDataTable.setCodeTypeData(_zjlxCodeType, _zjlxList);
                    // /== 
                    window.fdDataTable.setCodeTypeData(_zzlxCodeType, _zzlxList);
                    resolve('');
                }, function () {
                    reject(new Error(''));
                });
        });
    }
};
