export default {
    data() {
        return {
            // 
            popupTitle: '',
            // 
            popupStatus: this.showCxx,
            // 
            popupConfirm:{
                show: true,
                text: '',
                callback() {
                    window.console.log('');
                    // popup.hidePopup();
                }
            }
        };
    },
    methods: {
        // 
        setPopupStatus(flag) {
            this.popupStatus = flag;
        },
        // 
        showCxxPopup(dataItem, showCxxModelSource) {
            // 1.
            this.dSetShowCxxModelSource(showCxxModelSource);
            // 2.  
            //  JSON.stringify() 
            this.dSetCurrentCxxDataItem(JSON.parse(JSON.stringify(dataItem)));
            // 3. 
            this.updateTree(this.dCurrentCxxDataItem);
            // 3.
            this.setPopupStatus(true);
        },

        //  
        clickCancel() {
            // 1.
            this.dResetCxxData();
        },
        // 
        clickConfirm() {
            // 
            if (this.beforeClickHander(null, this.jsTreeObject)) {
                // debugger
                const _cxxList = this.dGetCxxList();
                let _flag = false;
                _cxxList.forEach(e => {
                    if (e.id === '002002001' && e.type === '') {
                        window.Artery.message.warning('');
                        _flag = true;
                    }
                });
                if (_flag) {
                    return;
                }
                // 1.
                this.emitGlobalEvent(() => {
                    // 
                    const _cxxList = this.dGetCxxList();
                    // 
                    this.globalBus.$emit(this.dShowCxxModelSource, _cxxList);
                });
                // 2.
                this.dResetCxxData();
                // 3.
                this.setPopupStatus(false);
            }
        }
    }
};
