/**
 * @namespace sl   search-list  
 *
 */
export default {
    props: {
        // 
        limit: {
            type: Number,
            default: 10
        },
        // 
        showEmptyText: {
            type: Boolean,
            default: false
        },
        // 
        emptyText: {
            type: String,
            default: ''
        },
        //   
        multipleSearch: {
            type: Boolean,
            default: true
        }
    },
    // 
    data() {
        return {
            // 
            slCurrentData: [

                /* {
                name: '',
                item: {},
                label: '<em class="fd-hightlight"></em>'
            }*/
            ],
            // 
            slVisible: false
        };
    },
    methods: {
        // 
        slSetVisible(flag) {
            this.slVisible = flag;
            // 
            if (!flag) {
                this.slSetCurrentData([]);
            }
        },
        // 
        slHide() {
            // 
            if (this.slVisible) {
                this.slSetVisible(false);
            }
        },
        // 
        slSetCurrentData(dataList) {
            this.slCurrentData = dataList;
        },
        // 
        slGetSearchPattern(query) {
            // const pattern = new RegExp(this.currentValue.replace(/(\^|\(|\)|\[|\]|\$|\*|\+|\.|\?|\\|\{|\}|\|)/g, '\\$1'), 'igm');
            // 
            if (this.multipleSearch && query) {
                // 
                const _query = query.split(' ').filter((item) => item !== '')
                    .join('|');
                return new RegExp(`(${_query})`, 'igm');
            }
            return new RegExp(`(${query})`, 'igm');
        },
        // 

        slFilterData(query, dataList) {
            const _dataList = [];
            // 
            const _pattern = this.slGetSearchPattern(query);
            // 
            let _atcActiveIndex = -1;
            // 
            dataList.every((item) => {
                // 
                if (_pattern.test(item[this.dName])) {
                    // const _label = _labelOrigin.replace(_pattern, '<em class="fd-hightlight">$1</em>');
                    const _label = item[this.dName].replace(_pattern, '<em class="fd-hightlight">$1</em>');
                    // 
                    const _data = {
                        //  name
                        [this.dName]: item[this.dName],
                        // label
                        label: _label,
                        // 
                        item: item
                    };
                    // 
                    if (item[this.dName] === query) {
                        _atcActiveIndex = _dataList.length;
                    }
                    // 
                    _dataList.push(_data);
                }
                //  
                return _dataList.length < this.limit;
            });
            // 
            // index
            this.atcSetActiveIndex(_atcActiveIndex);
            // 
            return _dataList;
        },
        // 
        slOperateData(dataList) {
            // 
            const _dataList = this.slFilterData(this.currentValue, dataList);
            // 
            this.slSetCurrentData(_dataList);
        },

        // 
        slSelectListItem(item) {
            // 
            this.setCurrentItem(item);
            // 1.value
            /* if (item[this.dName] !== this.value) {
                // 
                this.setCurrentItem(item);
            }*/
            // 2. 
            // this.hideSearchList();
            // 3. 
            // this.handleInput();
        },

        //  
        slRemoteSearch: function (query) {
            // 
            this.setRemoting(true);
            // 
            if (typeof this.remoteRequestMethod === 'function') {
                this.remoteRequestMethod(query).then((dataList) => {
                    // 
                    this.slOperateData(dataList);
                })
                    .finally(() => {
                        // 
                        this.setRemoting(false);
                    });
            } else {
                // 
                this.requestRemoteData(query).then((dataList) => {
                    // 
                    this.slOperateData(dataList);
                })
                    .finally(() => {
                        // 
                        this.setRemoting(false);
                    });
            }
        }
    }
};
