/**
 *@version 1.0.0
 *@author wanghuifeng
 *@createTime 2020//12/24 - 10:57
 *@updateTime 2020/12/29 - 10:57
 *@see [jsDoc]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description    => 
 */
export default {
    methods: {
        // 
        initTreeData() {
            // 
            this.requestCxxAsideTreeData()
                .then(serverData => {
                    // 
                    this.jsTreeDataList = serverData.data;
                    // 
                    this.jsTreeObject.update(this.jsTreeDataList);
                });
        },
        // 
        readyTree(treeObject) {
            this.jsTreeObject = treeObject;
        },
        // 
        updateTree(dataItem) {
            // 
            this.jsTreeObject.update(
                this.jsTreeDataList,
                this.dCurrentCxxDataItem,
                () => {
                    // 
                    this.jsTreeObject.mWatchValueHandle(dataItem.cxxList);
                }
            );
        },
        // 
        selectTree(item) {
            // 1. 
            this.dSetPreSelectedItem(this.dSelectedItem);
            // 2. 
            this.dSetSelectedItem(item);
            // 3. 
            this.operateConnectItemData(item);
        },
        // 
        changeTree(checkedDataList) {
            // console.log(checkedDataList);
            // 1. 
            this.dSetCxxList(checkedDataList);
        },
        // 
        operateConnectItemData(item) {
            // 3.2. id
            switch (item.id) {
                // 1. => 
                case '004004001':
                    // 1.1 
                    this.nsrdjxxItemConnectHandler(item);
                    break;
                // 1.2 => 
                case '004004003':
                    // 1.1 
                    this.zzszyfpxxItemConnectHandler();
                    break;
                // 2.
                // 2.1
                case '001003002':
                    this.zqcyItemConnectHandler(item);
                    break;
                // 2.2
                case '001003003':
                    this.zqcybdItemConnectHandler(item);
                    break;
                default:
                    break;
            }
        },
        //  => 
        zqcyItemConnectHandler(item) {
            // 1.
            // -1.1 
            //  ----1.1.1     
            //  ----1.1.2     @wanghuifeng 20210129
            // -1.2 
            // ----1.2.1 
            // ----1.2.2 
            if (item.checked) {
                // 1.1.2     @wanghuifeng 20210129
                if (item.id === this.dPreSelectedItem.id) {
                    item.checked = false;
                }
                // 
            } else {
                // 1.2.1 
                item.checked = true;
                // 1.2.2 
                // otherPropertystartDate 
                //     
                this.jsContentObject.updateDateGroup(
                    item.otherProperty.startDate
                        ? item.otherProperty.startDate
                        : Date.now() - 86400000, //eslint-disable-line
                    ''
                );
            }
            // 
            this.jsTreeObject.mUpdateItemCheckedStatus(item);
        },
        //  => 
        zqcybdItemConnectHandler(item) {
            // 1. 
            // -1.1 
            //  ----1.1.1     
            //  ----1.1.2     @wanghuifeng 20210129
            // -1.2 
            // ----1.2.1 
            // ----1.2.2 
            if (item.checked) {
                // 1.1.2     @wanghuifeng 20210129
                if (item.id === this.dPreSelectedItem.id) {
                    item.checked = false;
                }
                // 
            } else {
                item.checked = true;
                // 1. otherProperty
                if (
                    item.otherProperty.startDate &&
                    item.otherProperty.endDate
                ) {
                    //  
                    this.jsContentObject.updateDateGroup(
                        item.otherProperty.startDate,
                        item.otherProperty.endDate
                    );
                } else {
                    // 102021.1.152011.1.15
                    const currentStartDate = new Date().setFullYear(
                        new Date().getFullYear() - 10 // eslint-disable-line
                    );
                    //  
                    this.jsContentObject.updateDateGroup(
                        currentStartDate,
                        Date.now()
                    );
                }
            }
            // 
            this.jsTreeObject.mUpdateItemCheckedStatus(item);
        },
        //  => 
        nsrdjxxItemConnectHandler(dataItem) {
            //  02 =>
            //  ||
            if (
                this.dCurrentCxxDataItem.category === '02' &&
                (/^(01|02)$/gim).test(this.dCurrentCxxDataItem.childCategory)
            ) {
                //  => 004004003  
                const _targetItem = this.jsTreeObject.cGetCache('004004003');
                //  
                if (dataItem.checked && _targetItem.checked) {
                    _targetItem.checked = false;
                    // 2. 
                    this.jsTreeObject.mUpdateItemCheckedStatus(_targetItem);
                }
            }
        },
        //  => 
        zzszyfpxxItemConnectHandler() {
            //  02 =>
            //  ||
            if (
                this.dCurrentCxxDataItem.category === '02' &&
                (/^(01|02)$/gim).test(this.dCurrentCxxDataItem.childCategory)
            ) {
                //  => 004004001  
                const _targetItem = this.jsTreeObject.cGetCache('004004001');
                // 1.
                _targetItem.checked = true;
                // 2. 
                this.jsTreeObject.mUpdateItemCheckedStatus(_targetItem);
            }
        },
        // 
        beforeClickHander(item, asideTree) {
            let _flag = true;
            if (asideTree.activeItem.id) {
                // id 
                switch (asideTree.activeItem.id) {
                    // => , 
                    case '001001002':
                        //  
                        if (
                            (asideTree.activeItem.otherProperty.startDate &&
                                !asideTree.activeItem.otherProperty.endDate) ||
                            (!asideTree.activeItem.otherProperty.startDate &&
                                asideTree.activeItem.otherProperty.endDate)
                        ) {
                            _flag = false;
                            const _name =
                                asideTree.activeItem.id === '001001002'
                                    ? ''
                                    : '';
                            const _info = asideTree.activeItem.otherProperty
                                .startDate
                                ? ''
                                : '';
                            // 
                            this.globalBus.$emit(
                                'global-spb-invalid',
                                `${_name}${_info}`,
                                'warning'
                            );
                        }
                        break;
                    //  => , 
                    case '001003003': // eslint-disable-line
                        //      
                        if (item && item.id === asideTree.activeItem.id && asideTree.activeItem.checked) {
                            break;
                        }
                        // 
                        if (asideTree.activeItem.checked && (!asideTree.activeItem.otherProperty.endDate ||
                            !asideTree.activeItem.otherProperty.startDate)) {
                            _flag = false;
                            const _name =
                                asideTree.activeItem.id === '001001002'
                                    ? ''
                                    : '';
                            const _info = asideTree.activeItem.otherProperty
                                .startDate
                                ? ''
                                : '';
                            // 
                            this.globalBus.$emit(
                                'global-spb-invalid',
                                `${_name}${_info}`,
                                'warning'
                            );
                            break;
                        }
                        let start = new Date(asideTree.activeItem.otherProperty.startDate);
                        let end = new Date(asideTree.activeItem.otherProperty.endDate);
                        start.setFullYear(start.getFullYear() + 10);
                        if (start < end) {
                            //   10
                            _flag = false;
                            this.globalBus.$emit(
                                'global-spb-invalid',
                                `10`,
                                'warning'
                            );
                        }
                        break;
                    //  => , 
                    case '001003002':
                        // 
                        // idid
                        if (
                            asideTree.activeItem.checked &&
                            !asideTree.activeItem.otherProperty.startDate
                        ) {
                            // 1. item
                            // 1.1 idid
                            // 2. item
                            if (item) {
                                if (asideTree.activeItem.id !== item.id) {
                                    _flag = false;
                                }
                            } else {
                                _flag = false;
                            }
                            // 
                            if (!_flag) {
                                // 
                                this.globalBus.$emit(
                                    'global-spb-invalid',
                                    '',
                                    'warning'
                                );
                            }
                        }
                        break;
                    case '001002002':
                        // 
                        break;
                    default:
                        break;
                }
            }
            return _flag;
        }
    }
};
