export default {
    methods: {
        readyContent(contentObject) {
            // 
            this.jsContentObject = contentObject;
        },
        // 
        updateContent() {
            if (this.jsContentObject) {
                //  selectItem 
                this.jsContentObject.watchSelectItem(this.dSelectedItem, this.jsTreeObject, this.dCurrentCxxDataItem);
            }
        },
        // 
        changCxxContent(checkedDatalist) {
            // 
            this.dSelectedItem.checkedDatalist = checkedDatalist;
        }
    }
};
