import Vue from 'vue';
import Router from 'vue-router';
import pageUnit from '@/pages/page-unit/index.vue';
import pageSpb from '@/pages/page-spb/index.vue';
// 
import pageCxx from '@/components/app/module-spb/cxx/index.vue';
// 
const pageTest = {
    components: {pageCxx: pageCxx},
    template: '<pageCxx showCxx></pageCxx>'
};
// 
Vue.use(Router);
// 
export default new Router({
    routes: [
        {
            path: '/',
            redirect: 'spb'
        },
        {
            path: '/unit',
            name: 'pageUnit',
            component: pageUnit
        },
        {
            path: '/test',
            name: 'pageTest',
            component: pageTest
        },
        {
            path: '/spb',
            name: 'pageSpb',
            component: pageSpb
        }
    ]
});
